.home{
    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-left:auto;
    margin-right:auto;
    /* max-width: 1500px;   */
}
.home__row{
    display: flex;
    flex-wrap: wrap;
    z-index: 1;
    /* margin-left: 5px;
    margin-right: 5px; */
}
.home__image{
    width:100%;
    height: 10%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0.6));
}
.home_item{
    width: 20vw;
    margin: 40px;
}